import React, {useEffect, useRef, useState} from 'react'

export const HorizontalScroll = ({children, className}) => {
    const wrapper = useRef(null);

    const scroll = () => {
        wrapper.css('transform', 'translateX(100%)')
    }

    return (
        <div ref={wrapper} onScroll={scroll} className={`horizontal-scroll ${className}`}>
            <div className="horizontal-scroll__container">
                {children}
            </div>
        </div>
    )
}